.converter {
  position: absolute;
  width: 83vh;
  height: 20vh;
}

.result {
  position: absolute;
  background: red;
  width: 50%;
  height: 50%;
  bottom: -50%;
  left: 70%;
  transform: translateX(-50%);
}
.btn {
  width: 3vh;
  height: 5vh;
  border: none;
  margin: 0;
  padding: 0;
  background: none;
  font-size: 2.5vh;
  color: white;
}
.btnbtn:nth-child(4n + 5) {
  margin-left: 1.75vh;
}
.btnbtn:nth-child(25) {
  margin-left: 0vh;
}
.btn:active {
  background: grey;
}

.button_container {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.marker {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  width: 100%;
  height: 100%;
  pointer-events: none;
  top: 10%;
}

.marker > h1 {
  font-size: 3vh;
}
.marker > h1:nth-child(1) {
  margin-right: 39vh;
  margin-left: 1.5vh;
}
.marker > h1:nth-child(2),
.marker > h1:nth-child(5) {
  margin-right: 33vh;
}
.marker > h1:nth-child(4) {
  margin-right: 37.8vh;
  margin-left: 1vh;
}

input {
  font-size: 7vh;
  background: none;
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(255, 166, 0, 0.555);
  color: white;
  margin-bottom: 5vh;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}
